import { KpiCard } from "@/components/dashboard/kpi-card";
import { MiniBarChart } from "@/components/dashboard/mini-bar-chart";
import { SectionCard } from "@/components/dashboard/section-card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { getComercialMockData } from "@/data/comercial";

export default function ComercialPage() {
  const data = getComercialMockData("30d");

  return (
    <div className="space-y-6">
      <section className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <KpiCard kpi={data.kpis.vendasTotais} variant="currency" />
        <KpiCard kpi={data.kpis.ticketMedio} variant="currency" />
        <KpiCard kpi={data.kpis.novosClientes} />
        <KpiCard kpi={data.kpis.taxaConversao} variant="percentage" />
      </section>

      <section className="grid gap-4 md:grid-cols-2">
        <SectionCard
          title="Vendas por mês"
          subtitle="Tendência de faturamento"
        >
          <MiniBarChart values={data.vendasPorMes.map((p) => p.value)} />
        </SectionCard>

        <SectionCard
          title="Vendas por canal"
          subtitle="Distribuição da receita por canal"
        >
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Canal</TableHead>
                <TableHead>Vendas</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {data.vendasPorCanal.map((canal) => (
                <TableRow key={canal.canal}>
                  <TableCell>{canal.canal}</TableCell>
                  <TableCell>
                    {canal.valor.toLocaleString("pt-BR", {
                      style: "currency",
                      currency: "BRL",
                      maximumFractionDigits: 0,
                    })}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </SectionCard>
      </section>

      <section>
        <SectionCard
          title="Principais clientes"
          subtitle="Top contas por faturamento"
        >
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Cliente</TableHead>
                <TableHead>Faturamento</TableHead>
                <TableHead>Margem</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {data.principaisClientes.map((cliente) => (
                <TableRow key={cliente.id}>
                  <TableCell>{cliente.nome}</TableCell>
                  <TableCell>
                    {cliente.faturamentoMensal.toLocaleString("pt-BR", {
                      style: "currency",
                      currency: "BRL",
                      maximumFractionDigits: 0,
                    })}
                  </TableCell>
                  <TableCell>{cliente.margemPercentual.toFixed(1)}%</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </SectionCard>
      </section>
    </div>
  );
}


